.conversation-item{
    text-align: left;
    width: 100%;
    padding:10px 20px 5px;
    background-color: white; 
    box-sizing: border-box; 
    border-radius: 5px;
    border-bottom: .25rem solid transparent;
    box-shadow: 5px 6px 6px -9px rgba(0, 0, 0, 0.75);
    transition: all .20s cubic-bezier(.47,0,.745,.715);
    &-titleSection{
        display: flex;
        flex-direction: row;
        align-items: center;
        span{
            font-size: 1.5em;
        }
    }
    &:hover{ 
        border-bottom: 0.25rem solid red;
    }
}
